.destinations__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  column-gap: 16px;
  row-gap: 20px;
  justify-content: flex-start;
  align-items: center;
}

@media screen and (max-width: 896px) {
  .destinations__list {
    justify-content: center;
    margin: 0 19px;
  }
}
