.modal-container__close-button {
  display: flex;
  width: 100%;
  height: 100%;
  grid-area: button;
  align-items: flex-end;
  justify-content: flex-start;
}

@media screen and (max-width: 896px) {
  .modal-container__close-button {
    align-items: flex-end;
    justify-content: flex-end;
  }
}
